<template>
  <main class="mt-5">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <h5 class="mb-5 text-start">BASIC INFOMATION</h5>
          <form class="text-start">
            <div class="form-group mb-5">
              <label class="mb-2">EMAIL ADDRESS</label>
              <input
                type="email"
                class="form-control"
                placeholder="(/, 4~16)@example.com"
                v-model="t_user.email"
              />
            </div>
            <div class="form-group mb-5">
              <label class="mb-2">PASSWORD</label>
              <input
                type="password"
                class="form-control"
                placeholder="( //  2  , 10~16)"
                v-model="t_user.password"
              />
            </div>
            <div class="form-group mb-5">
              <label class="mb-2">NAME</label>
              <input type="text" class="form-control" v-model="t_user.name" />
            </div>
            <div class="form-group mb-5">
              <label class="mb-2">ADDRESS</label>
              <input
                type="text"
                class="form-control"
                v-model="t_user.address"
              />
            </div>
            <div class="form-group mb-5">
              <label class="mb-2">MOBILE</label>
              <input type="text" class="form-control" v-model="t_user.phone" />
            </div>
            <div class="form-group mb-2">
              <label>REFUND ACCOUNT INFOMATION</label>
            </div>
            <div class="input-group mb-2">
              <div class="input-group-prepend">
                <span class="input-group-text"></span>
              </div>
              <input
                type="text"
                class="form-control"
                v-model="t_user.account_holder"
              />
            </div>
            <div class="input-group mb-2">
              <label class="input-group-text"></label>
              <select class="form-select" v-model="t_user.bank_name">
                <option :key="i" v-for="(bank, i) in bankList">
                  {{ bank.bank_name }}
                </option>
              </select>
            </div>
            <div class="input-group mb-5">
              <div class="input-group-prepend">
                <span class="input-group-text"></span>
              </div>
              <input
                type="text"
                class="form-control"
                placeholder="('-'  .)"
                v-model="t_user.bank_account_number"
              />
            </div>
            <div class="d-flex justify-content-between align-items-center">
              <div class="col-6 d-grid p-1">
                <button
                  type="button"
                  class="btn btn-lg btn-outline-secondary"
                  @click="goToLogin"
                >
                  
                </button>
              </div>
              <div class="col-6 d-grid p-1">
                <button
                  type="button"
                  class="btn btn-lg btn-outline-secondary"
                  @click="memberJoin"
                >
                  
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </main>
</template> ->

<script>
export default {
  data() {
    return {
      t_user: {
        email: "",
        password: "",
        name: "",
        address: "",
        phone: "",
        account_holder: "",
        bank_name: "",
        bank_account_number: "",
      },
      bankList: [],
      // bank_name: [],
      // bank: ""
    };
  },
  computed: {
    user() {
      return this.$store.state.user; // user     user() 
    },
  },
  created() {
    this.getBankList(); // created  bankList data  
  },
  methods: {
    goToLogin() {
      //    /login 
      this.$router.push({ path: "/login" });
    },
    async getBankList() {
      // bankList sql  bankList  
      let bankList = await this.$api("/api/bankList", {});
      this.bankList = bankList;

      // let oBank = {};
      // bankList.forEach(item => {
      //   oBank[item.bank_name] = item.id;
      // });

      // let bank_name = [];
      // for(let key in oBank) {
      //   bank_name.push(key);
      // }
      // console.log(bank_name);

      // this.bank_name = bank_name;
    },
    memberJoin() {
      if (this.t_user.email == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.t_user.password == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.t_user.name == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.t_user.address == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.t_user.mobile == "") {
        //       alert 
        return this.$swal("   .");
      }

      if (this.t_user.account_holder == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.t_user.bank_account_number == "") {
        //      alert 
        return this.$swal("  .");
      }

      // this.t_user.bank_name_id = this.bankList.filter(c => {
      //   return (c.bank_name == this.bank);
      // })[0].id;

      // console.log(this.t_user.bank_name_id);

      this.$swal
        .fire({
          //     sweetAlert2  alert 
          title: " ?",
          showCancelButton: true,
          confirmButtonText: "JOIN",
          cancelButtonText: "CANCEL",
        })
        .then(async (result) => {
          if (result.isConfirmed) {
            await this.$api("/api/memberJoin", { user: this.t_user }) // t_user  memberJoin sql  Insert
              .then(() => {
                // api     alert  /login 
                this.$swal.fire("Saved.", "", "success");
                this.$router.push({ path: "/login" });
              })
              .catch(() => {
                //   ,  alert 
                alert(" .    .");
              });
          }
        });
    },
  },
};
</script>