<template>
  <main class="mt-5">
    <div class="container">
      <h4 class="text-center mb-5">Product Management</h4>
      <div class="float-end mb-2">
        <button type="button" class="btn btn-outline-dark" @click="goToInsert">
          
        </button>
      </div>
      <table class="table table-bordered">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr :key="i" v-for="(product, i) in productList">
            <td>
              <img
                :src="`/download/${product.id}/${product.path}`"
                style="height: auto; width: 80px"
              />
            </td>
            <td class="align-middle">{{ product.product_name }}</td>
            <td class="align-middle">
              {{ getCurrencyFormat(product.product_price) }}
            </td>
            <td class="align-middle">
              <button
                type="button"
                class="btn btn-outline-secondary me-1"
                @click="goToImageInsert(product.id)"
              >
                
              </button>
              <button
                type="button"
                class="btn btn-outline-success me-1"
                @click="goToUpdate(product.id)"
              >
                
              </button>
              <button
                type="button"
                class="btn btn-outline-danger"
                @click="deleteProduct(product.id)"
              >
                
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    //  data template     data 
    return {
      productList: [],
    };
  },
  created() {
    this.getProductList(); // created  getProductList  data  
  },
  methods: {
    //  
    getCurrencyFormat(value) {
      //  ,  $currencyFormat 
      return this.$currencyFormat(value);
    },
    async getProductList() {
      // getProductList  
      this.productList = await this.$api("/api/productList2", {}); // url  app.js /api/:alias  sql productList2 data 
      console.log(this.productList); //    
    },
    goToInsert() {
      this.$router.push({ path: "/create" }); // /create 
    },
    goToDetail(product_id) {
      //       router 
      this.$router.push({ path: "/detail", query: { product_id: product_id } }); // path /detail  product_id    
    },
    goToUpdate(product_id) {
      //      router 
      this.$router.push({ path: "/update", query: { product_id: product_id } });
    },
    goToImageInsert(product_id) {
      //       router 
      this.$router.push({
        path: "/image_insert",
        query: { product_id: product_id },
      });
    },
    deleteProduct(product_id) {
      //    sweetAlert 
      this.$swal
        .fire({
          title: " ?",
          showCancelButton: true,
          confirmButtonText: "DELETE",
          cancelButtonText: "CANCEL",
        })
        .then(async (result) => {
          if (result.isConfirmed) {
            await this.$api("/api/productDelete", { param: [product_id] }); // product_id data  , sql productDelete  data 
            this.getProductList(); // getProductList  
            this.$swal.fire("Deleted.", "", "success");
          }
        });
    },
  },
};
</script>