<template>
  <main class="mt-5">
    <div class="container">
      <div v-if="cartList == 0" class="row">
        <h5 class="m-4"> .</h5>
      </div>
      <div v-else>
        <table class="table table-bordered">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr :key="i" v-for="(product, i) in cartList">
              <td>
                <img
                  :src="`/download/${product.id}/${product.path}`"
                  style="height: auto; width: 80px"
                />
              </td>
              <td class="align-middle">{{ product.product_name }}</td>
              <td class="align-middle">
                {{ getCurrencyFormat(product.product_price) }}
              </td>
              <td class="align-middle">
                <div>{{ product.quantity }}</div>
              </td>
              <td class="align-middle">
                <div>{{ getCurrencyFormat(product.totalPrice) }}</div>
              </td>
              <td class="align-middle">
                <div>
                  <button
                    type="button"
                    class="btn btn-outline-danger"
                    @click="deleteCart(product.cart_id)"
                  >
                    
                  </button>
                </div>
              </td>
            </tr>
            <tr class="align-middle">
              <th colspan="6" style="text-align: right; font-size: large">
                  {{ getCurrencyFormat(total) }} + 
                {{ getCurrencyFormat(deliveryPrice) }} =  
                {{ getCurrencyFormat(paymentAmount) }}
              </th>
            </tr>
          </tbody>
        </table>
        <div class="col-12 mt-5">
          <button type="button" class="btn btn-outline-dark" @click="goToOrder">
            
          </button>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    //  data template     data 
    return {
      cartList: {},
      cartId: "",
      total: 0,
      cartItem: 0,
      deliveryPrice: 0,
      paymentAmount: 0,
    };
  },
  computed: {
    user() {
      return this.$store.state.user; // user     user() 
    },
  },
  mounted() {
    if (this.user.email == undefined) {
      // user email    
      alert("    .");
      this.$router.push({ path: "/" });
    }
  },
  created() {
    this.cartId = this.$store.state.user.email; // store   cartId 
    this.getCartList(); // created  getCartList()  cartList   
  },
  methods: {
    //  
    getCurrencyFormat(value) {
      //  ,  $currencyFormat 
      return this.$currencyFormat(value);
    },
    async getCartList() {
      // getCartList  
      let cartList = await this.$api("/api/cartList", { param: [this.cartId] }); // url  app.js /api/:alias  sql cartList data 
      this.cartList = cartList;
      console.log(this.cartList); //    

      let total = 0;
      cartList.forEach((item) => {
        total += item.totalPrice; //      
      });
      this.total = total;

      let deliveryPrice = 0;
      if (total >= 100000) {
        // total  10   0
        deliveryPrice = 0;
      } else {
        deliveryPrice = 3000; //    3000
      }
      this.deliveryPrice = deliveryPrice;

      let paymentAmount = total + deliveryPrice; // total       
      this.paymentAmount = paymentAmount;
    },
    goToOrder() {
      this.$router.push({ path: "/order" }); // /order 
    },
    deleteCart(cart_id) {
      // cart_id  sweetAlert2  cartList  
      this.$swal
        .fire({
          title: " ?",
          showCancelButton: true,
          confirmButtonText: "DELETE",
          cancelButtonText: "CANCEL",
        })
        .then(async (result) => {
          if (result.isConfirmed) {
            await this.$api("/api/cartDelete", { param: [cart_id] });
            this.getCartList();
            this.$swal.fire("Deleted.", "", "success");
          }
        });
    },
  },
};
</script>