<template>
  <main class="mt-5">
    <div class="container">
      <div class="row justify-content-center">
        <div
          class="col-sm-4 border m-3 p-4"
          @click="goToOrder(user_email)"
          style="cursor: pointer"
        >
          <h5 class="text-start p-2"> ORDER</h5>
          <div class="text-start p-2">
                  .
          </div>
        </div>
        <div
          class="col-sm-4 border m-3 p-4"
          @click="goToProfile(user_email)"
          style="cursor: pointer"
        >
          <h5 class="text-start p-2"> PROFILE</h5>
          <div class="text-start p-2">
                . <br />
                    
            .
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  computed: {
    user() {
      return this.$store.state.user; // user     user() 
    },
  },
  mounted() {
    if (this.user.email == undefined) {
      // user email    
      alert("    .");
      this.$router.push({ path: "/" });
    }
  },
  created() {
    this.user_email = this.$store.state.user.email; //   email user_email 
  },
  methods: {
    //  
    goToOrder(user_email) {
      this.$router.push({
        path: "/mypage_order",
        query: { user_email: user_email },
      }); //  user_email path       
    },
    goToProfile(user_email) {
      this.$router.push({
        path: "/mypage_profile",
        query: { user_email: user_email },
      }); //  user_email path       
    },
  },
};
</script>