<template>
  <main class="mt-3">
    <div class="container">
      <div class="row">
        <div class="col-md-6">
          <div
            id="carouselExampleIndicators"
            class="carousel slide"
            data-bs-ride="carousel"
          >
            <div class="carousel-indicators">
              <button
                type="button"
                data-bs-target="#carouselExampleIndicators"
                data-bs-slide-to="0"
                class="active"
                aria-current="true"
                aria-label="Slide 1"
              ></button>
              <button
                type="button"
                data-bs-target="#carouselExampleIndicators"
                data-bs-slide-to="1"
                aria-label="Slide 2"
              ></button>
              <button
                type="button"
                data-bs-target="#carouselExampleIndicators"
                data-bs-slide-to="2"
                aria-label="Slide 3"
              ></button>
            </div>
            <div class="carousel-inner">
              <div
                :class="`carousel-item ${i == 0 ? 'active' : ''}`"
                :key="i"
                v-for="(pimg, i) in productImage"
              >
                <img
                  :src="`/download/${productId}/${pimg.path}`"
                  class="d-block w-100"
                  alt="..."
                />
              </div>
            </div>
            <button
              class="carousel-control-prev"
              type="button"
              data-bs-target="#carouselExampleIndicators"
              data-bs-slide="prev"
            >
              <span
                class="carousel-control-prev-icon"
                aria-hidden="true"
              ></span>
              <span class="visually-hidden">Previous</span>
            </button>
            <button
              class="carousel-control-next"
              type="button"
              data-bs-target="#carouselExampleIndicators"
              data-bs-slide="next"
            >
              <span
                class="carousel-control-next-icon"
                aria-hidden="true"
              ></span>
              <span class="visually-hidden">Next</span>
            </button>
          </div>
        </div>
        <div class="col-md-6">
          <div class="card border-0 text-start">
            <div class="card-body">
              <h5 class="card-title">{{ productDetail.product_name }}</h5>
              <h5>
                <p class="card-text pt-3 pb-3">
                  {{ getCurrencyFormat(productDetail.product_price) }}
                </p>
              </h5>
              <div class="card-text pb-3 mb-4">
                <div class="row">
                  <div class="col-auto">
                    <nav style="padding-left: 0px">
                      <div class="nav nav-tabs" id="nav-tab" role="tablist">
                        <button
                          class="nav-link active text-body"
                          id="nav-home-tab"
                          data-bs-toggle="tab"
                          data-bs-target="#nav-home"
                          type="button"
                          role="tab"
                          aria-controls="nav-home"
                          aria-selected="true"
                        >
                          INSTRUCTION
                        </button>
                        <button
                          class="nav-link text-body"
                          id="nav-profile-tab"
                          data-bs-toggle="tab"
                          data-bs-target="#nav-profile"
                          type="button"
                          role="tab"
                          aria-controls="nav-profile"
                          aria-selected="false"
                        >
                          SIZE GUIDE
                        </button>
                        <button
                          class="nav-link text-body"
                          id="nav-contact-tab"
                          data-bs-toggle="tab"
                          data-bs-target="#nav-contact"
                          type="button"
                          role="tab"
                          aria-controls="nav-contact"
                          aria-selected="false"
                        >
                          SHIPPING
                        </button>
                      </div>
                    </nav>
                    <div class="tab-content" id="nav-tabContent">
                      <div
                        class="tab-pane fade show active pt-3"
                        style="white-space: pre-line"
                        id="nav-home"
                        role="tabpanel"
                        aria-labelledby="nav-home-tab"
                      >
                        {{ productDetail.product_description }}
                        <br /><br />[color] <br />{{
                          productDetail.product_color
                        }}
                        <br /><br />[Fabric] <br />{{
                          productDetail.product_fabric
                        }}
                        <br /><br />[Model] <br />{{
                          productDetail.product_model
                        }}
                      </div>
                      <div
                        class="tab-pane fade pt-3"
                        style="white-space: pre-line"
                        id="nav-profile"
                        role="tabpanel"
                        aria-labelledby="nav-profile-tab"
                      >
                        {{ productDetail.product_sizeGuide }} <br /><br />
                          1-3cm    .
                      </div>
                      <div
                        class="tab-pane fade pt-3"
                        id="nav-contact"
                        role="tabpanel"
                        aria-labelledby="nav-contact-tab"
                      >
                        -        
                        . <br />( 20   ,   
                         ) <br /><br />
                        -   3-7   , 
                              .
                        <br /><br />
                        -  3,000 ( 100,000    
                        ) <br /><br />
                        -        
                        . <br /><br />
                        -     . <br />
                        (  7    )
                        <br /><br />
                        -     ,  .
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="card-text pb-3">
                <div class="row">
                  <div class="col-auto">
                    <label class="col-form-label"></label>
                  </div>
                  <div class="col-auto">
                    <div class="input-group">
                      <span
                        class="input-group-text"
                        style="cursor: pointer"
                        @click="calculatePrice(-1)"
                        >-</span
                      >
                      <input
                        type="text"
                        class="form-control"
                        style="width: 40px"
                        v-model="total"
                      />
                      <span
                        class="input-group-text"
                        style="cursor: pointer"
                        @click="calculatePrice(1)"
                        >+</span
                      >
                    </div>
                  </div>
                </div>
              </div>
              <div class="row pt-3 pb-3">
                <div class="col-6">
                  <h3>  </h3>
                </div>
                <div class="col-6" style="text-align: right">
                  <h3>{{ getCurrencyFormat(totalPrice) }}</h3>
                </div>
              </div>
              <div class="d-flex justify-content-between align-items-center">
                <div class="col-12 d-grid p-1">
                  <button
                    type="button"
                    class="btn btn-lg btn-outline-dark"
                    @click="addCart"
                  >
                    ADD CART
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-6">
          <img
            :src="`/download/${productId}/${productDetail.path}`"
            class="img-fluid"
            style="width: 1000px"
          />
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    //  data template     data 
    return {
      productId: 0,
      productDetail: {},
      productImage: [],
      total: 0,
      totalPrice: 0,
    };
  },
  created() {
    this.productId = this.$route.query.product_id; // path  product_id  
    this.getProductDetail(); // created  getProductDetail  data  
    this.getProductImage(); // created  getProductImage  data  
  },
  methods: {
    //  
    calculatePrice(cnt) {
      let total = this.total + cnt; // cnt  (total)  (total  = 0)
      if (total < 0) total = 0; // total 0   total  0
      this.total = total;
      this.totalPrice = this.productDetail.product_price * this.total; // totalPrice   * 
    },
    getCurrencyFormat(value) {
      //  ,  $currencyFormat 
      return this.$currencyFormat(value);
    },
    async getProductDetail() {
      // getProductDetail  
      let productDetail = await this.$api("/api/productDetail", {
        param: [this.productId],
      }); // url  app.js /api/:alias  productId    sql productDatail data 
      if (productDetail.length > 0) {
        // productDetail   
        this.productDetail = productDetail[0]; // productDetail  
      }
      console.log(this.productDetail); //    
    },
    async getProductImage() {
      // getProductImage  
      this.productImage = await this.$api("/api/productMainImages", {
        param: [this.productId],
      }); // url  app.js /api/:alias  productId    sql productMainImages data 
      console.log(this.productImage);
    },
    async addCart() {
      if (this.total == 0) {
        //  0   alert 
        return this.$swal("1    .");
      }
      this.$swal
        .fire({
          title: " ? <br/>   .",
          showCancelButton: true,
          confirmButtonText: "ADD CART",
          cancelButtonText: "CANCEL",
        })
        .then(async (result) => {
          if (result.isConfirmed) {
            await this.$api("/api/cartInsert", {
              param: [
                this.productId,
                this.$store.state.user.email,
                this.total,
                this.totalPrice,
              ],
            }); // productId,  , ,   , sql cartInsert  data Insert
            this.$swal.fire("Saved.", "", "success");
            this.$router.push({ path: "/cart" }); // /cart 
          }
        });
    },
  },
};
</script>