<template>
  <main class="mt-3">
    <div class="container">
      <h2 class="text-center">  </h2>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label">ID</label>
        <div class="col-md-9">
          {{ productId }}
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          {{ productDetail.product_name }}
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <div class="row">
            <div
              class="col-lg-3 col-md-4 col-sm-2"
              :key="i"
              v-for="(m, i) in productImage.filter((c) => c.type == 1)"
            >
              <div class="position-relative">
                <img
                  :src="`/download/${productId}/${m.path}`"
                  class="img-fluid"
                />
                <div
                  class="position-absolute top-0 end-0"
                  style="cursor: pointer"
                  @click="deleteImage(m.id)"
                >
                  X
                </div>
              </div>
            </div>
          </div>
          <input
            type="file"
            class="form-control"
            accept="image/png,image/jpeg"
            @change="uploadFile($event.target.files, 1)"
          />
          <div class="alert alert-secondary" role="alert">
            <ul>
              <li>  : 350*350</li>
              <li>  : 1M </li>
              <li>  : png, jpg </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <div class="row">
            <div
              class="col-lg-3 col-md-4 col-sm-2"
              :key="i"
              v-for="(m, i) in productImage.filter((c) => c.type == 2)"
            >
              <div class="position-relative">
                <img
                  :src="`/download/${productId}/${m.path}`"
                  class="img-fluid"
                />
                <div
                  class="position-absolute top-0 end-0"
                  style="cursor: pointer"
                  @click="deleteImage(m.id)"
                >
                  X
                </div>
              </div>
            </div>
          </div>
          <input
            type="file"
            class="form-control"
            accept="image/png,image/jpeg"
            @change="uploadFile($event.target.files, 2)"
          />
          <div class="alert alert-secondary" role="alert">
            <ul>
              <li> 5 </li>
              <li>  : 700*700</li>
              <li>  : 1M </li>
              <li>  : png, jpg </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <div class="row">
            <div class="col-lg-6 col-md-8">
              <input
                type="file"
                class="form-control"
                accept="image/png,image/jpeg"
                @change="uploadFile($event.target.files, 3)"
              />
              <div class="alert alert-secondary" role="alert">
                <ul>
                  <li>  : 5M </li>
                  <li>  : png, jpg </li>
                </ul>
              </div>
            </div>
            <div
              class="col-lg-6 col-md-4"
              :key="i"
              v-for="(m, i) in productImage.filter((c) => c.type == 3)"
            >
              <div class="position-relative">
                <img
                  :src="`/download/${productId}/${m.path}`"
                  class="img-fluid"
                />
                <div
                  class="position-absolute top-0 end-0"
                  style="cursor: pointer"
                  @click="deleteImage(m.id)"
                >
                  X
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="mb-3 row m-auto">
        <button
          type="button"
          class="btn btn-lg btn-outline-dark"
          @click="goToList"
        >
          CONFIRM
        </button>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    //  data template     data 
    return {
      productId: 0,
      productName: "",
      productDetail: {},
      productImage: [],
    };
  },
  computed: {
    user() {
      return this.$store.state.user; // user     user() 
    },
  },
  created() {
    // created     data  
    this.productId = this.$route.query.product_id; // path  product_id  
    this.getProductDetail();
    this.getProductImage();
  },
  mounted() {
    if (this.user.email == undefined) {
      // user email    
      alert("    .");
      this.$router.push({ path: "/" });
    }
  },
  methods: {
    goToList() {
      // confirm   /sales   
      this.$router.push({ path: "/sales" });
    },
    async getProductDetail() {
      // getProductDetail  
      let productDetail = await this.$api("/api/productDetail", {
        param: [this.productId],
      }); // url  app.js /api/:alias  productId    sql productDatail data 
      if (productDetail.length > 0) {
        // productDetail    data 
        this.productDetail = productDetail[0]; // productDetail  
      }
    },
    async getProductImage() {
      // getProductImage  
      this.productImage = await this.$api("/api/imageList", {
        param: [this.productId],
      }); // url  app.js /api/:alias  productId    sql imageList data 
      console.log(this.productImage);
    },
    deleteImage(id) {
      // productImage id , sweetAlert2     alert 
      this.$swal
        .fire({
          title: "  ?",
          showCancelButton: true,
          confirmButtonText: ``,
          cancelButtonText: ``,
        })
        .then(async (result) => {
          if (result.isConfirmed) {
            await this.$api("/api/imageDelete", { param: [id] }); // productImage id imageDelete sql   productImage  delete
            this.getProductImage(); // delete getProductImage 
            this.$swal.fire("!", "", "success");
          }
        });
    },
    async uploadFile(files, type) {
      //  files type   
      let name = "";
      let data = null;
      if (files) {
        // files 
        name = files[0].name; // 0  
        data = await this.$base64(files[0]); // files $base64   
      }
      const { error } = await this.$api(
        `/upload/${this.productId}/${type}/${name}`,
        { data }
      ); //  productId type, name, data api 
      if (error) {
        //   
        return this.$swal("  .  .");
      }
      this.$swal("  ."); //   
      setTimeout(() => {
        // 1  getProudctImage() 
        this.getProductImage();
      }, 1000);
    },
  },
};
</script>