<template>
  <main class="mt-3">
    <div class="container">
      <h2 class="text-center mb-3"> </h2>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <input
            type="text"
            class="form-control"
            v-model="product.product_name"
          />
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"> </label>
        <div class="col-md-9">
          <div class="input-group">
            <input
              type="number"
              class="form-control"
              v-model="product.product_price"
            />
            <span class="input-group-text"></span>
          </div>
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"> </label>
        <div class="col-md-9">
          <textarea
            class="form-control"
            rows="7"
            v-model="product.product_description"
          ></textarea>
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <input
            type="text"
            class="form-control"
            v-model="product.product_color"
          />
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <input
            type="text"
            class="form-control"
            v-model="product.product_fabric"
          />
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"> </label>
        <div class="col-md-9">
          <input
            type="text"
            class="form-control"
            v-model="product.product_model"
          />
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <input
            type="text"
            class="form-control"
            v-model="product.product_size"
          />
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"> </label>
        <div class="col-md-9">
          <textarea
            class="form-control"
            rows="3"
            v-model="product.product_sizeGuide"
          ></textarea>
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"> </label>
        <div class="col-md-9">
          <select class="form-select" v-model="cate">
            <option :value="cate" :key="i" v-for="(cate, i) in category">
              {{ cate }}
            </option>
          </select>
        </div>
      </div>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <input type="text" class="form-control" v-model="product.tags" />
        </div>
      </div>
      <div class="mb-3 row">
        <div class="d-flex justify-content-between align-items-center">
          <div class="col-6 d-grid p-1">
            <button
              type="button"
              class="btn btn-lg btn-outline-dark"
              @click="goToList"
            >
              CANCEL
            </button>
          </div>
          <div class="col-6 d-grid p-1">
            <button
              type="button"
              class="btn btn-lg btn-outline-dark"
              @click="productInsert"
            >
              SAVE
            </button>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    return {
      product: {
        product_name: "",
        product_price: 0,
        product_description: "",
        product_color: "",
        product_fabric: "",
        product_model: "",
        product_size: "",
        product_sizeGuide: "",
        tags: "",
        seller_id: 1,
        category_id: 1,
      },
      categoryList: [],
      category: [],
      cate: "",
    };
  },
  computed: {
    user() {
      return this.$store.state.user; // user     user() 
    },
  },
  created() {
    this.getCategoryList(); // created  getCategoryList  data  
  },
  mounted() {
    if (this.user.email == undefined) {
      // user email    
      alert("    .");
      this.$router.push({ path: "/" });
    }
  },
  methods: {
    goToList() {
      this.$router.push({ path: "/sales" }); //    , /sales 
    },
    async getCategoryList() {
      let categoryList = await this.$api("/api/categoryList", {}); // sql categoryList  data 
      this.categoryList = categoryList;

      let oCategory = {}; // oCategory  
      categoryList.forEach((item) => {
        oCategory[item.category] = item.id; // oCategory categoryList data forEach  , item.id 
      });

      let category = []; // category  
      for (let key in oCategory) {
        category.push(key); // category oCategory key 
      }
      console.log(category);

      this.category = category;
    },
    productInsert() {
      if (this.product.product_name == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.product.product_price == "" || this.product.product_price == 0) {
        //    0   alert 
        return this.$swal(" .");
      }

      if (this.product.product_description == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.product.product_color == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.product.product_fabric == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.product.product_model == "") {
        //        alert 
        return this.$swal("    .");
      }

      if (this.product.product_size == "") {
        //      alert 
        return this.$swal("  .");
      }

      if (this.product.product_sizeGuide == "") {
        //       alert 
        return this.$swal("   .");
      }

      this.product.category_id = this.categoryList.filter((c) => {
        // product category_id category cate    categoryList data 0 id 
        return c.category == this.cate;
      })[0].id;

      console.log(this.product.category_id);

      this.$swal
        .fire({
          //     sweetAlert2 
          title: " ?",
          showCancelButton: true,
          confirmButtonText: "REGISTRATION",
          cancelButtonText: "CANCEL",
        })
        .then(async (result) => {
          if (result.isConfirmed) {
            await this.$api("/api/productInsert", { param: [this.product] }); // product data  , sql productInsert  data Insert
            this.$swal.fire("Saved.", "", "success");
            this.$router.push({ path: "/sales" }); // /sales 
          }
        });
    },
  },
};
</script>