<template>
  <main class="mt-5">
    <div class="container">
      <div v-if="orderList == 0" class="row">
        <h5 class="m-4"> .</h5>
      </div>
      <div v-else>
        <table class="table table-bordered">
          <thead>
            <tr>
              <th>[]</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr :key="i" v-for="(product, i) in orderList">
              <td class="align-middle">
                {{ product.created_date }} [{{ product.order_id }}]
              </td>
              <td class="align-middle">
                <img
                  :src="`/download/${product.id}/${product.path}`"
                  style="height: auto; width: 80px"
                />
              </td>
              <td class="align-middle">{{ product.product_name }}</td>
              <td class="align-middle">
                {{ getCurrencyFormat(product.product_price) }}
              </td>
              <td class="align-middle">
                <div>{{ product.quantity }}</div>
              </td>
              <td class="align-middle">
                <div>{{ getCurrencyFormat(product.totalPrice) }}</div>
              </td>
              <td class="align-middle">
                <div v-if="product.delivery_status == ''">
                   
                </div>
                <div v-else-if="product.delivery_status == ''">
                   
                </div>
                <div v-else-if="product.delivery_status == ''"></div>
                <div v-else-if="product.delivery_status == ''">
                   
                </div>
                <div v-else-if="product.delivery_status == ''">
                  <div class="mb-2"></div>
                  <div>CJ </div>
                  <div
                    style="cursor: pointer; text-decoration: underline"
                    @click="goToDelivery(product.transport_document_number)"
                  >
                    [{{ product.transport_document_number }}]
                  </div>
                </div>
                <div v-else>
                  <div class="mb-2"> </div>
                  <div>CJ </div>
                  <div
                    style="cursor: pointer; text-decoration: underline"
                    @click="goToDelivery(product.transport_document_number)"
                  >
                    [{{ product.transport_document_number }}]
                  </div>
                </div>
              </td>
              <td class="align-middle">
                <div v-if="product.delivery_status == ''">
                  <div style="font-size: 10px" class="mb-1">
                     <br />  , <br />.
                  </div>
                  <button
                    class="btn btn-outline-danger"
                    @click="goToCancle(product.cart_id)"
                  >
                    
                  </button>
                </div>
                <div v-else-if="product.delivery_status == ''">
                  <button class="btn btn-outline-danger"></button>
                </div>
                <div v-else>-</div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    //  data template     data 
    return {
      orderList: {},
      user_email: "",
      total: 0,
      cartItem: 0,
      deliveryPrice: 0,
      paymentAmount: 0,
    };
  },
  created() {
    this.user_email = this.$store.state.user.email; //   email user_email 
    this.getCartList(); // created  getCartList  data  
  },
  methods: {
    //  
    getCurrencyFormat(value) {
      //  ,  $currencyFormat 
      return this.$currencyFormat(value);
    },
    async getCartList() {
      // getCartList  
      let orderList = await this.$api("/api/orderList", {
        param: [this.user_email],
      }); // url  app.js /api/:alias  sql orderList     data 
      this.orderList = orderList;
      console.log(this.orderList); //    

      let total = 0;
      orderList.forEach((item) => {
        total += item.totalPrice; //      
      });
      this.total = total;

      let deliveryPrice = 0;
      if (total >= 100000) {
        // total  10   0
        deliveryPrice = 0;
      } else {
        deliveryPrice = 3000; //    3000
      }
      this.deliveryPrice = deliveryPrice;

      let paymentAmount = total + deliveryPrice; // total       
      this.paymentAmount = paymentAmount;
    },
    goToOrder() {
      this.$router.push({ path: "/order" }); //    , /order 
    },
    goToDelivery(delivery_address) {
      //    ,     
      window.open(
        "http://nplus.doortodoor.co.kr/web/detail.jsp?slipno=" +
          delivery_address
      );
    },
    async goToCancle(cart_id) {
      // cart_id  sweetAlert2 
      this.$swal
        .fire({
          title: " ?",
          showCancelButton: true,
          confirmButtonText: "YES",
          cancelButtonText: "CANCEL",
        })
        .then(async (result) => {
          if (result.isConfirmed) {
            await this.$api("/api/orderCancle", { param: [cart_id] }); //  cart_id data sql orderCancle   
            this.$swal.fire("Saved.", "", "success");
            this.$router.go(); // 
          }
        });
    },
  },
};
</script>