<template>
  <main class="mt-5">
    <div class="container">
      <div v-if="orderList == 0" class="row">
        <h5 class="m-4"> .</h5>
      </div>
      <div v-else>
        <h4 class="text-center mb-5">Order Management</h4>
        <table class="table table-bordered">
          <thead>
            <tr>
              <th>[]</th>
              <th> </th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr :key="i" v-for="(order, i) in orderList">
              <td class="align-middle">
                {{ order.created_date }} [{{ order.id }}]
              </td>
              <td class="align-middle">{{ order.user_email }}</td>
              <td class="align-middle">{{ order.name_of_depositor }}</td>
              <td class="align-middle">
                {{ getCurrencyFormat(order.payment_amount) }}
              </td>
              <td class="align-middle">{{ order.status }}</td>
              <td class="align-middle">
                CJ<br />{{ order.transport_document_number }}
              </td>
              <td>
                <button
                  class="btn btn-outline-dark"
                  @click="goToDetail(order.order_id)"
                >
                   
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    //  data template     data 
    return {
      orderList: {},
      statusList: [],
    };
  },
  created() {
    // created     data  
    this.getOrderList();
    this.getStatusList();
  },
  methods: {
    //  
    getCurrencyFormat(value) {
      //  ,  $currencyFormat 
      return this.$currencyFormat(value);
    },
    async getOrderList() {
      // getOrderList  
      let orderList = await this.$api("/api/orderManagement"); // url  app.js /api/:alias  sql orderManagement  data 
      this.orderList = orderList;
      console.log(this.orderList); //    
    },
    async getStatusList() {
      let statusList = await this.$api("/api/statusList"); // url  app.js /api/:alias  sql statusList  data 
      this.statusList = statusList;
    },
    goToDetail(order_id) {
      this.$router.push({
        path: "/order_detail",
        query: { order_id: order_id },
      }); //  order_id path       
    },
    // async goToCancle(cart_id) {
    //   this.$swal.fire({
    //     title: ' ?',
    //     showCancelButton: true,
    //     confirmButtonText: 'YES',
    //     cancelButtonText: 'CANCEL'
    //   }).then(async (result) => {
    //     if (result.isConfirmed) {
    //       await this.$api("/api/orderCancle",{param:[cart_id]});
    //       this.$swal.fire('Saved.', '', 'success');
    //       this.$router.go();
    //     }
    //   });
    // }
  },
};
</script>